import { Button, Center, Container, PasswordInput, Stack, TextInput } from "@mantine/core";

// 1. Добавить импорт
import { useRegisterMutation } from '@/lib/store/api/authApi';
import { useState } from 'react';

export default function Registration() {
  // 2. Добавить стейты для полей
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  
  // 3. Получить мутацию регистрации
  const [register, { isLoading }] = useRegisterMutation();

  // 4. Создать функцию отправки
  const handleSubmit = async () => {
    try {
      const result = await register({ email, password }).unwrap();
      // 5. Обработать успех (редирект или уведомление)
      console.log('Успешная регистрация:', result);
    } catch (error) {
      // 6. Обработать ошибку
      console.error('Ошибка регистрации:', error);
    }
  };

  return (
    <Center h={200}>
      <Container size="sm">
        <Stack w={400}>
          <TextInput
            label="Почта"
            placeholder="Иванов@mail.ru"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <PasswordInput
            label="Пароль"
            placeholder="Пароль" 
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          {/* 7. Добавить обработчик на кнопку */}
          <Button 
            onClick={handleSubmit}
            loading={isLoading}
          >
            Зарегистрироваться
          </Button>
        </Stack>
      </Container>
    </Center>
  )
}